.taroify-floating-bubble {
  position: fixed;
  left: 0;
  top: 0;
  right: var(--floating-bubble-initial-gap, 48px);
  bottom: var(--floating-bubble-initial-gap, 48px);
  width: var(--floating-bubble-size, 96px);
  height: var(--floating-bubble-size, 96px);
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  touch-action: none;
  background: var(--floating-bubble-background, var(--blue, #1989fa));
  color: var(--floating-bubble-color, var(--white, #fff));
  border-radius: var(--floating-bubble-border-radius, 999px);
  z-index: var(--floating-bubble-z-index, 999);
  transition: var(--floating-bubble-duratio, 0.3s);
}
.taroify-floating-bubble:active {
  opacity: 0.8;
}
.taroify-floating-bubble__icon {
  font-size: var(--floating-bubble-icon-size, 56px);
}